<template>
    <div class="my-page-blog">
        <MyPageBlogItem
            v-for="(item, index) in $store.state.blog.myBlogs"
            :key="index"
            :blog="item"
            :handleView="handleView"
            :handleDelete="handleDelete"
            :handleEdit="handleEdit"
        />
    </div>
</template>

<script>
export default {
    data() {
        return {
            something: ""
        };
    },
    activated() {
        this.$store.dispatch("getMyBlog");
    },
    mounted() {
        // this.$store.dispatch("getMyBlog");
    },
    methods: {
        handleView(id) {
            this.$router.push("/show-blog/" + id);
        },
        handleDelete(id) {
            console.log(id);
        },
        handleEdit(id) {
            this.$router.push({
                path: "/add-blog?type=edit&id=" + id
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.my-page-blog {
    width: 100%;
    height: 100%;
}
</style>