<template>
    <div class="blog-show">
        <HomeHeader class="show-header" />
        <div class="article">
            <div class="article-header">
                <div class="title">
                    {{ blog.title }}
                </div>
                <div class="article-author-edit">
                    <div class="article-author">
                        <img
                            :src="baseIMGURL + userid + '?byuserid=1'"
                            class="ueser-head"
                        />
                        <span class="user-name">{{ blog.username }}</span>
                        <span class="time">{{ blog.createdAt | date1 }}</span>
                        <span class="views">阅读数 {{ blog.views }}</span>
                        <span class="like">收藏</span>
                    </div>
                    <div
                        v-show="user._id === blog.userid"
                        @click.stop="handleEdit"
                        class="article-edit"
                    >
                        编辑
                    </div>
                </div>
            </div>
            <div class="markdown-body article-detail" v-html="html"></div>
            <!-- 提前加载高亮样式 -->
            <mavon-editor
                codeStyle="atelier-plateau-light"
                style="display:none"
            />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            blog: {
                html: ""
            }
        };
    },
    mounted() {
        let id = this.$route.params.id;
        this.getBlogById(id);
    },
    computed: {
        html() {
            return this.blog.html;
        },
        userid() {
            return this.blog.userid || "default.png";
        }
    },
    methods: {
        async getBlogById(id) {
            let blog = await this.$store.dispatch("getBlogById", id);
            this.blog = blog;
        },
        handleEdit() {
            this.$router.push({
                path: "/add-blog?type=edit&id=" + this.blog._id
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.blog-show {
    height: 100%;
    width: 100%;

    @media screen and (max-width: 500px) {
        width: 100vw;
        height: 100vh;
        margin: 0;
        padding: 0;
        .show-header {
            display: none;
        }
        .time {
            // position: absolute;
        }
        .article-edit {
            display: none;
        }
    }
    .article {
        padding-top: 44px;
        padding: 44px 1rem 0 1rem;
        margin: 0 auto;
        max-width: 962px;
        background-color: #fff;
        width: 100%;
        div.article-header {
            padding: 1rem;
            box-sizing: border-box;
            z-index: 9;
            background-color: #fff;
            width: 100%;
            div.title {
                height: 45px;
                line-height: 45px;
                font-size: 28px;
                font-weight: bold;
                margin: 18.76px 0;
            }

            div.article-author-edit {
                font-size: 14px;
                height: 40px;
                padding: 1rem;
                border-bottom: 1px solid #f5f6f7;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                div.article-author {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    position: relative;
                    img.ueser-head {
                        width: 30px;
                        height: 30px;
                        border-radius: $uni-border-radius-circle;
                        margin-right: $uni-spacing-col-sm;
                    }
                    span {
                        margin-right: $uni-spacing-col-base;
                    }
                    // span.user-name {
                    // }

                    // span.time {
                    // }

                    // span.views {
                    // }

                    // span.like {
                    // }
                }

                div.article-edit {
                    padding: 0 4px;
                    letter-spacing: 1px;
                    cursor: pointer;
                    color: $uni-color-primary;
                    &:hover {
                        color: $uni-color-error;
                    }
                }
            }
        }
        .article-detail {
            background-color: #fff;
            padding: 1rem;
        }
    }
}

</style>